<template>
  <div class="main">
    <div class="sidebar">
      <div class="sidebar-inner" v-if="!anchor === false">
        <h3>{{anchor.title}}</h3>
        <ul class="menu-root">
          <li v-for="item in anchor.anchor" :key="item">
            <a :href="'#' + item">{{item}}</a>
          </li>
        </ul>
      </div>
    </div>
    <div class="content">
      <div class="docContainer">
        <h2 id="搭建 ss">CentOS 7 搭建shadowsocks(ss)服务器</h2>
        <section>
          <p>在搭建ss服务器之前，先谈一下CentOS 7与CentOS 6在主要使用上的区别。</p>
          <p>1、服务管理由service命令变为systemctl命令。支持start、stop和status 3种动作。</p>
          <p>2、防火墙由iptables变为firewalld。使用firewall-cmd进行操作。firewalld的配置请参考如何在RHEL/CentOS 7以及Fedora中配置FirewallD</p>
        </section>
        <h2 id="安装 ss">安装 shadowsocks</h2>
        <section>
          <p>1、这是两个步骤，先安装python-setuptools,然后安装pip,easy_install是setuptools里的一个命令，实际上是在调用setuptools完成安装模块的作用</p>
          <div class="code">yum install python-setuptools & easy_install pip</div>
          <p>2、升级 pip (可不升)</p>
          <div class="code">pip install –upgrade pip</div>
          <p>3、shadowsocks安装时是不分客户端还是服务器端的，只不过安装后有两个脚本一个是sslocal代表以客户端模式工作，一个是ssserver代表以服务器端模式工作.)</p>
          <div class="code">pip install shadowsocks </div>
        </section>
        <h2 id="配置 ss">配置 shadowsocks</h2>
        <section>
          <div class="code">vim /etc/shadowsocks.json</div>
          <p>配置的json文件路径就是之后ssserver启动路径</p>
          <div class="code">
            {
              "server": "0.0.0.0",
              "server_port": 2222,
              "password": "123456",
              "local_address": "127.0.0.1",
              "local_port": 1080,
              "timeout": 600,
              "method": "aes-256-cfb",
              "workers": 1
            }
          </div>
          <div class="code">
            {
              "server": "0.0.0.0",
              "port_password": {
                "2222": "123456",
                "3333": "123456"
              },
              "local_address": "127.0.0.1",
              "local_port": 1080,
              "timeout": 600,
              "method": "aes-256-cfb",
              "fast_open": false,
              "workers": 2
            }
          </div>
        </section>
        <h2 id="启动 ss">启动 shadowsocks</h2>
        <section>
          <p>ssserver 有三个参数 start stop restart</p>
          <div class="code">ssserver -c /etc/shadowsocks.json -d start</div>
          <p>如果不确定是否启动，可以使用如下命令查看</p>
          <div class="code">ps -aux | grep ssserver</div>
          <p>ps是 linux 查看系统进程的工具。如果像下图一样证明这个进程已经启动成功了。</p>
          <div style="margin: 10px"><img @click="clickPic(srcOne)" style="cursor: zoom-in;width:80%" :src="srcOne"></div>
        </section>
        <h2 id="创建 ss 服务">创建 shadowsocks 服务, 随系统启动</h2>
        <div>新建启动脚本文件vim /etc/systemd/system/shadowsocks.service</div>
        <ul class="code">
          <li>[Unit]</li>
          <li>Description=Shadowsocks Server</li>
          <li>After=network.target remote-fs.target nss-lookup.target</li>
          <hr>
          <li>[Service]</li>
          <li>Type=forking</li>
          <li>#设置启动时的配置文件,根据自己的需求改.</li>
          <li>ExecStart=/usr/bin/ssserver -c /etc/shadowsocks-libev/config.json -d start</li>
          <li>ExecReload=/bin/kill -HUP $MAINPID</li>
          <li>ExecStop=/usr/bin/ssserver -d stop</li>
          <hr>
          <li>[Install]</li>
          <li>WantedBy=multi-user.target</li>
        </ul>
        <p>执行以下命令启动 shadowsocks 服务</p>
        <div class="code">systemctl enable shadowsocks</div>
        <div class="code">systemctl start shadowsocks</div>
        <p>检查 shadowsocks 服务是否已成功启动，可以执行以下命令查看服务的状态</p>
        <div class="code">systemctl status shadowsocks -l</div>
        <h2 id="配置防火墙">配置防火墙</h2>
        <section>
          <ul class="code">
            <li>启动： systemctl start firewalld</li>
            <li>停止： systemctl disable firewalld</li>
            <li>禁用： systemctl stop firewalld</li>
            <li>查看状态： systemctl status firewalld </li>
          </ul>
        </section>
        <p>开启一个端口</p>
        <div class="code">firewall-cmd --zone=public --add-port=2222/tcp --permanent（--permanent永久生效，没有此参数重启后失效）</div>
        <p>此时firewalld还没有载入这个设置，我们需要重新载入规则</p>
        <div class="code">firewall-cmd --reload</div>
        <h2 id="成功">成功</h2>
        <p>世界那么大，快去看看吧</p>
        <div><img @click="clickPic(srcTwo)" :src="srcTwo" style="cursor: zoom-in;width:80%"></div>
        <h2 id="失败">失败</h2>
        <p>1、换较low的浏览器</p>
        <p>2、ECS服务器的话设置下安全组</p>
      </div>
      <div :class="['dialogWrap', popVisible ? 'show' : 'hide']">
        <div class="dialog-mask" @click="popVisible = false"></div>
        <div @click="popVisible = false"><img class="dialog-img" :src="currectSrc"></div>
      </div>
      <div class="share">
        <div class="share-wx">
          <qrcanvas class="qrcode" :options="options"></qrcanvas>
          <svg-icon class="wx" icon-name="wx"></svg-icon>
        </div>
      </div>
    </div>
  </div>
</template>

